const IepDoc = require('./IepDoc')
module.exports = IepApp

/**
 * Fonction crant un objet permettant de grer plusieurs figures instrumenPoche
 * dans des svg diffrents
 * @param {Object} [options]
 * @param {boolean} [options.debug] passer true pour avoir des infos de debug en console
 * @constructor
 */
function IepApp (options) {
  this.debug = Boolean(options && options.debug)
  if (this.debug) console.log('Mode debug activ')
  this.docs = []
  const self = this
  window.addEventListener('unload', () => self.closeAllXMLWindows())
}

/**
 * Fonction ajoutant un document   un iepApp
 * @param {string|Element} svg : Le svg (l'lment du dom ou son id) dans lequel vont se faire les affichages.
 * @param {string} chDoc : La chane XML dcrivant le document.
 * @param {boolean} [autoStart=true] Passer false pour ne pas dmarrer l'animation ds le chargement
 */
IepApp.prototype.addDoc = function (svg, chDoc, autoStart = true) {
  if (typeof svg === 'string') svg = document.getElementById(svg)
  if (!svg) return new Error('svg manquant')
  if (svg.localName !== 'svg') return new Error('svg incorrect')
  const options = {
    autoStart,
    debug: this.debug
  }
  const doc = new IepDoc(svg, chDoc, options)
  this.docs.push(doc)
  svg.iepDoc = doc // Pour que les boutons accdent au document.
  return doc
}

/**
 * Fonction utilise lors de la fermeture d'une fentre contenant des figures
 * InstrumenPoche dans des SVG.
 * Elle provoque la fermeture des ventuelles fentres popup contenant le code
 * XML d'une figure (obtenues en cliquant sur une icne XML)
 */
IepApp.prototype.closeAllXMLWindows = function () {
  for (let i = 0; i < this.docs.length; i++) {
    const doc = this.docs[i]
    if (doc.windowxml != null) doc.windowxml.close()
  }
}
