const ObjetBase = require('./objetBase')
module.exports = ObjetLigne
/**
 * Classe anctre de tous les objets de type ligne
 * @extends iep.objetBase
 * @constructor
 * @param {iep.iepDoc} doc : le document propritaire de l'objet
 * @param {string} id : l'id de l'objet dans le fichier XML de la figure
 * @param {string} couleur : la couleur de l'objet
 * @param {string} epaisseur : l'paisseur de trait
 * @param {string} opacite : l'opacit du trait
 * @param {string} styleTrait : "tiret" pour avoir des pointills
 */
function ObjetLigne (doc, id, couleur, epaisseur, opacite, styleTrait) {
  ObjetBase.call(this, doc, id, couleur)
  this.epaisseur = epaisseur
  this.opacite = (arguments.length >= 5) ? opacite : '100'
  this.styleTrait = (arguments.length >= 6) ? styleTrait : 'continu'
  const op = parseFloat(this.opacite / 100)
  this.style = 'stroke:' + couleur + ';stroke-width:' + epaisseur +
          ';stroke-opacity:' + op + ';stroke-linecap:round;'
  if (this.styleTrait === 'tiret') this.style += 'stroke-dasharray:10 8 10;' // Pointills
}
ObjetLigne.prototype = new ObjetBase()
