const kernel = require('../global/global')
const ObjetBase = require('./objetBase')
module.exports = Texte
/**
 * Classe reprsentant un texte.
 * L'criture dans le texte se fait via des actionEcrireTexte
 * @extends iep.objetBase
 * @constructor
 * @param {iep.iepDoc} doc : Le docmument propritaire
 * @param {string} id : L'id du texte
 * @param {string} x : L'asbsisse d'affichage
 * @param {string} y : L'ordonne d'affichage
 */
function Texte (doc, id, x, y) {
  ObjetBase.call(this, doc, id, 'black')
  this.x = parseFloat(x)
  this.y = parseFloat(y)
  // Il faut mmoriser la position initiale du texte car il peut avoir t traslat ar l'animation
  // et quand on la relance il doit remprendre sa place initiale
  this.xinit = this.x
  this.yinit = this.y

  this.taille = 20
  this.angle = 0 // Possibilit de tourner un etxte (pas donne avec la version Flash)
  this.texte = ''
  this.objet = 'texte'
}
Texte.prototype = new ObjetBase()

// Attention : POur un affichage de texte, il faut dcaler vers le bas de la hauteur de la police pour
// garder la compatibilit avec le flash
Texte.prototype.positionne = function () {
  // var y = this.y + parseFloat(this.taille);
  // this.g.setAttribute("transform","translate(" + this.x + "," + y + ")");
  this.setPosition(this.x, this.y, this.angle, this.zoomfactor)
}
Texte.prototype.translate = function (x, y) {
  this.x = x
  this.y = y
  this.positionne()
}
/** @inheritDoc */
Texte.prototype.updateg = function (g) {
  const oldg = this.g
  this.doc.svg.replaceChild(g, oldg)
  this.g = g
  g.setAttribute('visibility', 'visible')
}
/**
 * Fonction mettant dans this.g l'lment graphique reprsentant l'objet dans
 * le DOM du svg de la figure
 */
Texte.prototype.creeg = function () {
  this.g = document.createElementNS(kernel.svgsn, 'g')
}
/**
 * Fonction mettant le texte aux coordonnes (x,y) avec un angle angle et
 * un coefficient de zoom zoomfactor et modifiant la spoition du svg element qui
 * le reprsente dans le DOM du svg.
 * @param {float} x
 * @param {float} y
 * @param {float} angle
 * @param {integer} zoomfactor
 * @returns {undefined}
 */
Texte.prototype.setPosition = function (x, y, angle, zoomfactor) {
  this.x = x
  this.y = y
  this.angle = angle
  this.zoomfactor = zoomfactor
  const y2 = y + parseFloat(this.taille)
  this.g.setAttribute('transform', 'scale(' + zoomfactor + ') translate(' +
    String(x / zoomfactor) + ',' + String(y2 / zoomfactor) + ') rotate(' + angle + ')')
}
