const kernel = require('../global/global')
const DroiteAncetre = require('./droiteAncetre')
module.exports = Droite
/**
 * Classe reprsentant une droite donne par les coordonnes de ses deux points
 * @constructor
 * @extends iep.objetBase
 * @param {Float} x1 : Abscisse du premier point
 * @param {Float} y1 : Ordonne du deuime point
 * @param {Float} x2 : Abscisse du deuxime point
 * @param {Float} y2 : Ordonne du deuxime point
 * @param {string} couleur : La couleur
 * @param {string} epaisseur : L'paisseur de trait
 * @param {string} opacite : L'opacit du trac
 * @param {string} styleTrait : "tiret" pour un trait pointill
 */
function Droite (doc, id, x1, y1, x2, y2, couleur, epaisseur, opacite, styleTrait) {
  DroiteAncetre.call(this, doc, id, x1, y1, x2, y2, couleur, epaisseur, opacite, styleTrait)
}
Droite.prototype = new DroiteAncetre()

/**
 * Fonction mettant dans this.g l'lment graphique reprsentant l'objet dans
 * le DOM du svg de la figure
 */
Droite.prototype.creeg = function () {
  this.calcule()
  if (!this.horsFenetre) {
    const li = document.createElementNS(kernel.svgsn, 'line')
    li.setAttribute('x1', this.xext1)
    li.setAttribute('y1', this.yext1)
    li.setAttribute('x2', this.xext2)
    li.setAttribute('y2', this.yext2)
    li.setAttribute('style', this.style)
    this.g = li
  } else this.g = document.createElementNS(kernel.svgsn, 'g')
  this.g.setAttribute('visibility', 'hidden')
  // this.g.setAttribute("id",this.id);
}
/**
 * Calcul des coordonnes des points d'intersection de la droite avec les bords de la fentre
 * Si la droite est hors de la fentre, this.horsFenetre est mis   false, sinon true
 * Au retour, (this.xext1, this.yext1) et (this.xext2, this.yext2) sont les coodonnes des points au bord
 * @param {type} svg
 */
Droite.prototype.calcule = function () {
  let a, xa, ya, xb, yb, xc, yc, xd, yd, x1, y1, x2, y2
  const width = parseInt(this.doc.svg.getAttribute('width'))
  const height = parseInt(this.doc.svg.getAttribute('height'))
  x1 = this.x1
  y1 = this.y1
  x2 = this.x2
  y2 = this.y2
  if ((x1 === x2) && (y1 === y2)) {
    this.horsFenetre = true
    return
  }
  this.horsFenetre = false
  if (x1 === x2) {
    this.xext1 = x1
    this.yext1 = 0
    this.xext2 = x1
    this.yext2 = height
    if ((x1 < 0) || (x1 > width)) {
      this.horsFenetre = true
      return
    }
    this.xext1 = x1
    this.yext1 = 0
    this.xext2 = x1
    this.yext2 = height
    return
  }
  if (y1 === y2) {
    this.xext1 = 0
    this.yext1 = y1
    this.xext2 = width
    this.yext2 = y1
    if ((y1 < 0) || (y1 > height)) {
      this.horsFenetre = true
      return
    }
    this.xext1 = 0
    this.yext1 = y1
    this.xext2 = width
    this.yext2 = y1
    return
  }
  let indicePoint = 0
  a = (y2 - y1) / (x2 - x1)
  xa = 0
  ya = this.y1 + a * (xa - x1)
  if ((ya >= 0) && (ya <= height)) {
    indicePoint++
    this.xext1 = xa
    this.yext1 = ya
  }
  xb = width
  yb = a * (xb - x1) + y1
  if ((yb >= 0) && (yb <= height)) {
    indicePoint++
    if (indicePoint === 1) {
      this.xext1 = xb
      this.yext1 = yb
    } else {
      this.xext2 = xb
      this.yext2 = yb
      return
    }
  }
  yc = 0
  xc = (yc - y1) / a + x1
  if ((xc > 0) && (xc < width)) {
    indicePoint++
    if (indicePoint === 1) {
      this.xext1 = xc
      this.yext1 = yc
    } else {
      this.xext2 = xc
      this.yext2 = yc
      return
    }
  }
  yd = height
  xd = (yd - y1) / a + x1
  if ((xd > 0) && (xd <= width)) {
    indicePoint++
    if (indicePoint === 2) {
      this.xext2 = xd
      this.yext2 = yd
    } else {
      this.horsFenetre = true
    }
  } else this.horsFenetre = true
  if (this.horsFenetre) {
    this.xext1 = 0
    this.yext1 = ya
    this.xext2 = xb
    this.yext2 = yb
  }
}
