const kernel = require('../global/global')
const Droite = require('./droite')
const Vect = require('../types/vecteur')
module.exports = DemiDroite

/**
 * Classe reprsentant une demi-droite dans la figure InstrumenPoche
 * @extends iep.objetBase
 * @constructor
 * @param {iep.iepDoc} doc :le document propritaire de la figure
 * @param {type} id : l'id de l'objet dans le fichier XML
 * @param {Float} x1 : abscisse du premier point
 * @param {Float} y1 : ordonne du premier point
 * @param {Float} x2 : abscisse du second point
 * @param {Float} y2 : ordonne du premier point
 * @param {string} couleur : couelur de la demi-droite
 * @param {string} epaisseur : Epaisseur du trait
 * @param {string} opacite : Opacit du trait
 * @param {string} styleTrait : "tiret" pour avoir des pointills
 */
function DemiDroite (doc, id, x1, y1, x2, y2, couleur, epaisseur, opacite, styleTrait) {
  Droite.call(this, doc, id, x1, y1, x2, y2, couleur, epaisseur, opacite, styleTrait)
}
DemiDroite.prototype = new Droite()
// Inutile de redfinir creeg()
/**
 * Fonction calculant les coordonnes des points   la limite de la figure
 * (xext1,yext1) et (xext2,yext2) pour le trac rel
 */
DemiDroite.prototype.calcule = function () {
  let u, v
  Droite.prototype.calcule.call(this)
  const v1 = new Vect(this.x1, this.y1, this.x2, this.y2) // Vceteur directeur de la demi-droite
  // Si la droite est hors-fentre, la demi-droite l'est aussi
  if (this.horsFenetre) return
  // Dans IEP l'origine est toujours dans la fentre
  u = new Vect(this.x1, this.y1, this.xext1, this.yext1)
  if (u.presqueNul()) {
    v = new Vect(this.x1, this.y1, this.xext2, this.yext2)
    if (kernel.colineairesMemeSens(v, v1)) {
      this.xext1 = this.x1
      this.yext1 = this.y1
    } else {
      this.xext2 = this.xext1
      this.yext2 = this.yext1
      this.xext1 = this.x1
      this.yext1 = this.y1
    }
  } else {
    if (kernel.colineairesMemeSens(u, v1)) {
      this.xext2 = this.xext1
      this.yext2 = this.yext1
      this.xext1 = this.x1
      this.yext1 = this.y1
    } else {
      this.xext1 = this.x1
      this.yext1 = this.y1
    }
  }
}
