const kernel = require('../global/global')
const ObjetLigne = require('./objetLigne')
const Vect = require('../types/vecteur')
module.exports = AngleDroit
/**
 * Classe reprsentant une marque d'angle droit de la figure
 * @extends iep.objetBase
 * @constructor
 * @param {iep.iepDoc} doc : le document propitaire de la figure
 * @param {type} id : l'ide de l'objet dans le xml de la figure
 * @param {Float} xsommet : absisse du sommet de l'angle droit
 * @param {Float} ysommet : ordonne du sommet de l'angle droit
 * @param {Float} xinter : abscisse du sommet de la marque d'angle
 * @param {type} yinter : ordonne du sommet de la marque d'angle
 * @param {type} couleur : couleur de la marque
 * @param {type} epaisseur
 */
function AngleDroit (doc, id, xsommet, ysommet, xinter, yinter, couleur, epaisseur) {
  this.xsommet = parseFloat(xsommet)
  this.ysommet = parseFloat(ysommet)
  this.xinter = parseFloat(xinter)
  this.yinter = parseFloat(yinter)
  ObjetLigne.call(this, doc, id, couleur, epaisseur, 100)
  this.objet = 'angle_droit'
}
AngleDroit.prototype = new ObjetLigne()
/**
 * Fonction mettant dans this.g l'lment graphique reprsentant l'objet dans
 * le DOM du svg de la figure
 */
AngleDroit.prototype.creeg = function () {
  let v1 = new Vect(this.xsommet, this.ysommet, this.xinter, this.yinter)
  const n = v1.norme()
  v1 = v1.vecteurColineaire(n / Math.sqrt(2))
  v1 = v1.tourne(45)
  const v2 = v1.tourne(-90)
  const p = document.createElementNS(kernel.svgsn, 'polyline')
  const points = String(this.xsommet + v1.x) + ' ' + String(this.ysommet + v1.y) + ' ' +
    this.xsommet + ' ' + this.ysommet + ' ' + String(this.xsommet + v2.x) + ' ' + String(this.ysommet + v2.y)
  p.setAttribute('points', points)
  p.setAttribute('style', this.style + 'fill:none;')
  this.g = p
}
