module.exports = ActionAncetre
/**
 * Action ancetre de toutes les actions de la figure
 * @constructor
 * @param {iep.iepDoc} doc : le document contenant les objets
 * @param {string} tempo : Le tempo   attendre   la fin de l'action en diximes de seconde
 * avant de passer   l'action suivante. Si null on passe directement   l'action suivante.
 */
function ActionAncetre (doc, tempo) {
  if (arguments.length === 0) return
  this.doc = doc
  this.tempo = (tempo === null) ? null : parseInt(tempo) * 100 // tempo est en dizimes de seconde
  this.isReady = false // Sera mis   true une fois qu'on est sr que l'lment est bien charg et prt.
}
/**
 * Fonction renvoyant lorsque l'action a t charg et prte.
 * Est redfini pour les actions dont le chargement est asynchrone : images et actions
 * d'criture de texte ncessitant l'utilisation du LaTeX.
 */
ActionAncetre.prototype.setReady = function () {
  this.isReady = true
}
/**
 * Fonction qui n'aura une action que pour les actions de cration d'objet et crera l'lment graphique
 * du DOM associ Srea redfini pour les actions de cration d'objet
 */
ActionAncetre.prototype.creegElement = function () {
}
/**
 * Fonction renvoyant true seulement si l'action a une action visible sur la figure.
 * Lors de l'appel de iepDoc.ajouteAction, chaque action se voit attribuer un membre indice
 * qui est son indice dans la liste des actions cres..
 * Renvoie true par dfaut.
 * @returns {boolean}
 */
ActionAncetre.prototype.actionVisible = function () {
  return true
}
