/**
 * Document reprsentant une figure affiche dans le SVG d'id idDoc
 * @constructor
 * @param {Element} svg  Le SVG contenant la figure
 * @param {string} chdoc La chane contenant le code XML dcrivant la figure.
 * @param {boolean} [autoStart=false] : Si true l'animation dmarre automatiquement
 * @param {Object} [options]
 * @param {boolean} [options.debug] passer true pour avoir des infos de debug en console
 * @param {boolean} [options.autoStart=false] passer true pour dmarrer l'animation ds le chargement
 */
function IepDoc (svg, chdoc, options) {
  /*
   * L'lment html du svg
   * @type Element
   */
  this.svg = svg
  // pour la compatibilit ascendante, on traite le 3e argument boolen comme autostart
  if (typeof options === 'boolean') {
    this.autoStart = options
    options = {}
  } else {
    if (typeof options !== 'object') options = {}
    this.autoStart = Boolean(options.autoStart)
  }
  this.debug = Boolean(options.debug)
  /**
   * Pointera sur la fentre contenant le code xml
   * @type Window
   */
  this.windowxml = null
  let ch = chdoc
  // On ajoute l'en-tte xml si elle a t ommise.
  if (ch.search(/<?xml/gi) === -1) ch = '<?xml version="1.0" encoding="UTF-8"?>' + ch
  // On limine les <action_ suivis de chiffres
  ch = ch.replace(/<action[_]\d*/gi, '<action')
  // On limine les remarques html
  ch = ch.replace(/<!--.*-->/g, '')
  // On corrige les oublis d'espaces et les -- dans les attributs
  ch = ch.replace(/(="[^"]*")([^ ])/g, '$1 $2').replace(/--/g, '')
  // On corrige les ventuels << ou >>
  ch = ch.replace(/<</g, '<').replace(/>>/g, '>')
  // Certains auteurs ont mis des <actiontempo= sans espace ou autre
  ch = ch.replace(/<action(\w)/gi, '<action $1')
  // Les commentaires peuvent un attribut texte ce qui pose des problmes dans elimineDoublonsXML
  // On les remplace par un attribut texteCommentaire
  ch = ch.replace(/<commentaire\s*texte\s*=/gi, '<commentaire texteCommentaire =')
  // On corrige les attributs en doublon
  ch = kernel.elimineDoublonsXML(ch)
  // On limine l'en-tte qui contient parfois des caractres bizarres
  /**
   * La string xml du doc
   * @type string
   */
  this.codexml = ch // Mmoris pour pouvoir tre affiche quand on clique sur le bouton XML

  // on corrige le tag ouvrant et fermant, parfois folklorique dans les scripts existants
  const ind1 = ch.search(/<\s*INSTRUMENPOCHE/gi)
  const ind2 = ch.indexOf('>', ind1)
  const ind3 = ch.search(new RegExp('<\\s*/INSTRUMENPOCHE', 'gi')) // Pas de > dans la recherche car certains auteurs se sont tromps sur la balise de fin
  this.chdoc = '<INSTRUMENPOCHE>' + ch.substring(ind2 + 1, ind3) + '</INSTRUMENPOCHE>'

  this.actions = [] // Un tableau qui contiendra toutes les actions de la figure.
  // Une liste des lments graphiques de type objet graphique traits par la figure.
  // Chaque lment sera identifi par un n d'id et par son type
  // Lorsqu'un lment de mme id et de mme nature qu'un lment dj  existant est cr, lors de l'excution
  // de l'action de cration, son lment graphique remplacera celui de l'lement dj  existant
  // dans le DOM du svg de la figure
  this.elements = []
  this.compasRetourne = false
  // On cre les instruments ds le dbut car   chaque fois qu'un instrument est translat, tourn, zoom,
  // certains objet crs ont besoin de connatre l'tat des instruments pour leur cration
  this.compasLeve = null
  this.compas = new Compas(this)
  this.equerre = new Equerre(this)
  this.rapporteur = new Rapporteur(this)
  this.regle = new Regle(this)
  this.requerre = new Requerre(this)
  this.crayon = new Crayon(this)
  // On parse le xml
  this.xmldoc = kernel.parseXMLDoc(this.chdoc)
  this.tabact = this.xmldoc.getElementsByTagName('action')
  this.started = false
  this.animationEnCours = true // Sera mis   false pour le pas   pas
  // indiceActionEnCours est l'indice de la premire future action qui devra tre excute
  this.indiceActionEnCours = 0
  this.creeActions()
  const t = this
  // Les vnements souris pour pouvoir grer les dplacements de la barre d'icnes
  svg.addEventListener('mousemove', function (ev) { t.onmousemove(ev, ev.clientX, ev.clientY) }, false)
  svg.addEventListener('mouseup', function () { t.onmouseup() }, false)
  svg.addEventListener('mouseleave', function () { t.onmouseleave() }, false)
  svg.addEventListener('touchmove', function (ev) { t.onmousemove(ev, ev.touches[0].pageX, ev.touches[0].pageY) }, false)
  svg.addEventListener('touchend', function () { t.onmouseup() }, false)
  svg.addEventListener('touchcancel', function () { t.onmouseup() }, false)
  svg.addEventListener('touchleave', function () { t.onmouseleave() }, false)
  if (this.debug) console.log('iepDoc instanci :', this)
};

/**
 * Fontion apppele   la fin du chargement de certains onjets dont le chargement est asynchrone : les images
 * et les textes dont l'criture ncessite l'utilisation du LaTeX.
 * Renvoie true si tous les objets de la figure sont bien chargs.
 * Au bout de 10 secondes coules affiche un message d'erreur
 * @returns
 */
IepDoc.prototype.waitForReadyState = function () {
  if (this.started) return
  const ready = this.verifyReadyState()
  if (ready) {
    this.start()
  } else {
    const self = this
    setTimeout(function () {
      self.waitForReadyState()
    }, 200) // On vrifie toutes les 200ms
  }
}

/**
 * Fonction renvoyant true seulement si tous les objets de la figure sont bien chargs
 * @returns {boolean}
 */
IepDoc.prototype.verifyReadyState = function () {
  if (this.debug) {
    const pasPretes = this.actions.filter(action => !action.ready)
    if (pasPretes.length) console.log(`${pasPretes.length} actions pas encore prtes (sur ${this.actions.length})`)
    else console.log(`Les ${this.actions.length} actions sont prtes`)
  }
  return this.actions.every(action => action.isReady)
}

/**
 * Fonction crant tous les lments graphiques des aactions de cration de la figure.
 * Seules les actions de crations d'objets ont une fonction creegElement faisant quelquechose
 */
IepDoc.prototype.creeElementsGraphiques = function () {
  this.actions.forEach(action => action.creegElement())
}

/**
 * Fonction rinitilisant la position initiale de tous les instruments et les cachant.
 */
IepDoc.prototype.initialiseOutils = function () {
  outils.forEach(outil => {
    this[outil].initialisePosition()
    this[outil].montre(false)
  })
  this.compas.leve = false
}

/**
 * Fonction retirnat du DOM du SVG les lments graphiques des outils et les recrant
 * de faon   ce que les outils soient cres dans le DOM aprs les lments graphiques
 * des objets de la figure.
 */
IepDoc.prototype.recreeOutils = function () {
  outils.forEach(outil => {
    this[outil].updateg()
    this[outil].montre(false)
  })
  this.compasLeve = null
}

/**
 * Fonction lance aprs le chargement complet de la figure.
 * Toutes les actions de cration d'objet de la figure crent leur propre lment graphique dans le DOM du svg
 * qu'elles stockent dans une variable this.g. Ces lments sont initialeent tous cachs.
 * Ensuite on recre dans le DOM les lments graphiques des outils pour qu'ils soient situs aprs les objets crs.
 * Puis on cre les icnes et on les initialise   leur tat initial  pour le dmarrage de la figure.
 */
IepDoc.prototype.start = function () {
  this.started = true
  this.creeElementsGraphiques()
  this.recreeOutils()
  this.creeIcones()
  this.InitialiseIcones()
  if (this.autoStart) {
    this.onPlay()
  } else {
    this.montreIcone('Pause', false)
    this.montreIcone('Play', true)
  }
}

/**
 * Fonction crant les icnes de la figure.
 */
IepDoc.prototype.creeIcones = function () {
  // Toutes les images seront contenues dans un g element appel barreIcones
  const nbIcones = 8
  const t = this
  this.barreIcones = document.createElementNS(kernel.svgsn, 'g')
  this.creeElementsPourIcones()
  this.iconGoBegin = this.iconeGoBegin(0, 0, 'fonce')
  this.iconGoBeginGris = this.iconeGoBegin(0, 0, 'clair')
  this.iconGoBegin.onclick = function () { t.onGoBegin() }
  this.barreIcones.appendChild(this.iconGoBegin)
  this.barreIcones.appendChild(this.iconGoBeginGris)
  this.iconStepPrev = this.iconeStepPrev(32, 0, 'fonce')
  this.iconStepPrevGris = this.iconeStepPrev(32, 0, 'clair')
  this.iconStepPrev.onclick = function () { t.onStepPrev() }
  this.barreIcones.appendChild(this.iconStepPrev)
  this.barreIcones.appendChild(this.iconStepPrevGris)
  this.iconPause = this.iconePause(64, 0, 'fonce')
  this.iconPauseGris = this.iconePause(64, 0, 'clair')
  this.iconPause.onclick = function () { t.onPause() }
  this.barreIcones.appendChild(this.iconPause)
  this.barreIcones.appendChild(this.iconPauseGris)
  this.iconPlay = this.iconePlay(96, 0, 'fonce')
  this.iconPlayGris = this.iconePlay(96, 0, 'clair')
  this.iconPlay.onclick = function () { t.onPlay() }
  this.barreIcones.appendChild(this.iconPlay)
  // Icne rouge   activer quand on rencontre une pause
  // Cette icne ne sera visible et active que quand on rencontre une action de pause
  this.iconPlay2 = this.iconePlay(96, 0, 'rouge')
  this.iconPlay2.setAttribute('visibility', 'hidden')
  this.iconPlay2.onclick = function () { t.onPlayContinuer() }
  this.barreIcones.appendChild(this.iconPlay2)
  this.barreIcones.appendChild(this.iconPlayGris)
  this.iconRestart = this.iconeRestart(128, 0, 'fonce')
  this.iconRestartGris = this.iconeRestart(128, 0, 'clair')
  this.iconRestart.onclick = function () { t.onRestart() }
  this.barreIcones.appendChild(this.iconRestart)
  this.barreIcones.appendChild(this.iconRestartGris)
  this.iconStepNext = this.iconeStepNext(160, 0, 'fonce')
  this.iconStepNextGris = this.iconeStepNext(160, 0, 'clair')
  this.iconStepNext.onclick = function () { t.onStepNext() }
  this.barreIcones.appendChild(this.iconStepNext)
  this.barreIcones.appendChild(this.iconStepNextGris)
  this.iconGoEnd = this.iconeGoEnd(192, 0, 'fonce')
  this.iconGoEndGris = this.iconeGoEnd(192, 0, 'clair')
  this.iconGoEnd.onclick = function () { t.onGoEnd() }
  this.barreIcones.appendChild(this.iconGoEnd)
  this.barreIcones.appendChild(this.iconGoEndGris)
  this.iconXML = this.iconeXML(224, 0, 'fonce')
  this.iconXMLGris = this.iconeXML(224, 0, 'clair')
  this.iconXML.onclick = function () { t.onXML() }
  this.barreIcones.appendChild(this.iconXML)
  this.barreIcones.appendChild(this.iconXMLGris)

  this.hauteurBarre = 32
  this.demiLargeurBarre = nbIcones * 16
  // this.xbarre et this.ybarre contiennent les coordonnes initiales de la barre d'outils dans le SVG
  this.xbarre = 50 // Barre place   gauche de bord
  this.ybarre = 4
  this.barreIcones.setAttribute('transform', 'translate(' + this.xbarre + ',4)')
  this.svg.appendChild(this.barreIcones)
  // Ajout ge gestionnaires de souris sur la barre d'icnes
  this.isDraggingBarre = false
  this.barreIcones.addEventListener('mousedown', function (ev) { t.onmousedownbarre(ev.clientX, ev.clientY) }, false)
  this.barreIcones.addEventListener('mousemove', function (ev) { t.onmousemovebarre(ev.clientX, ev.clientY) }, false)
  // C'est le SVG qui gre les vnements mouseup
  this.barreIcones.addEventListener('touchstart', function (ev) { t.onmousedownbarre(ev.touches[0].pageX, ev.touches[0].pageY) }, false)
  this.barreIcones.addEventListener('touchmove', function () { t.onmousemovebarre() }, false)
  // C'est le svg qui gre les vnements touhcance et touchend
  // this.barreIcones.addEventListener("touchcancel",function() {t.onmouseupbarre()},false);
  // Initialisation de la position relative de la barre d'outils
  // this.deltax et this.deltay contiendront de dcalage relatif de la barre d'outils par rapport
  //   sa position initiale lorsqu'on la fait glisser.
  this.deltax = 0
  this.deltay = 0
}

/**
 * Fonction appele quand on clique ou fait un appui tactile sur la barre d'icnes de la figure
 * @param {Integer} xmouse : l'abscisse du point cliqu par la souris
 * @param {Integer} ymouse : l'ordonne du point cliqu par la souris
 */
IepDoc.prototype.onmousedownbarre = function (xmouse, ymouse) {
  if (this.animationEnCours) return
  this.isDraggingBarre = true
  this.xcapturebarre = xmouse
  this.ycapturebarre = ymouse
}

/**
 * Fonction appele au survol de la barre d'icnes par la souris ou du pointeur tactive
 */
IepDoc.prototype.onmousemovebarre = function () {
  if (this.animationEnCours) {
    this.barreIcones.style.cursor = 'default'
    this.isDraggingBarre = false
    return
  }
  this.barreIcones.style.cursor = 'pointer'
}

/**
 * Fonction appele au ssurvol du svg par la souris ou le pointeur tactile
 * @param {event} ev
 * @param {Integer} xmouse : l'abscisse pointe par la souris
 * @param {Integer} ymouse : l'ordonne pointe par la souris
 */
IepDoc.prototype.onmousemove = function (ev, xmouse, ymouse) {
  if (this.isDraggingBarre) {
    const w = parseInt(this.svg.getAttribute('width'))
    const h = parseInt(this.svg.getAttribute('height'))
    const dx = xmouse - this.xcapturebarre
    const dy = ymouse - this.ycapturebarre
    const deltax = this.deltax + dx
    const deltay = this.deltay + dy
    const xpos = this.xbarre + deltax
    const ypos = this.ybarre + deltay
    if ((xpos <= w - 2 * this.demiLargeurBarre) && (xpos >= 0) &&
            (ypos >= 0) && (ypos <= h - this.hauteurBarre)) {
      this.barreIcones.setAttribute('transform', 'translate(' + xpos + ',' + ypos + ')')
      this.deltax = deltax
      this.deltay = deltay
      this.xcapturebarre = xmouse
      this.ycapturebarre = ymouse
    }
    ev.preventDefault() // Pour viter que des lments texte soient slectionns en bougeant la souris
  }
  // ev.preventDefault(); // Pour viter que des lments texte soient slectionns en bougeant la souris
  // Abandonn car alors on e peut pls zoomer ou dzoomer sur priphrique mobile
}
/**
 * Fonction appele lorsque le bouton de la souris est relch sur le svg
 */
IepDoc.prototype.onmouseup = function () {
  this.isDraggingBarre = false
}
/**
 * Fonction appele quand on relche le bouton de la souris sur la figure
 */
IepDoc.prototype.onmouseleave = function () {
  this.isDraggingBarre = false
}
/**
 * Fonction appele quand on clique sur le bouton XML de la barre d'outils
 * Ouvre une fentre affichant le code XML de la figure
 */
IepDoc.prototype.onXML = function () {
  if (this.windowxml == null) this.windowxml = this.popup()
  else this.windowxml.focus()
}

/**
 * Fonction activant ou dsactivant l'icne de nom nomIcone
 * Si bvisible est true, l'icne normale est montre et l'icne grise est cache
 * Si bvisible est false,l'icne normale est masque et l'icne grise est montre
 * @param {string} nomIcone
 * @param {boolean} bvisible
 */
IepDoc.prototype.montreIcone = function (nomIcone, bvisible) {
  const nom = 'icon' + nomIcone
  this[nom].setAttribute('visibility', bvisible ? 'visible' : 'hidden')
  this[nom + 'Gris'].setAttribute('visibility', bvisible ? 'hidden' : 'visible')
}
/**
 * Fonction activant l'icne rouge Continuer et dsactivant les autres icnes
 */
IepDoc.prototype.activeIconeContinuer = function () {
  this.montreIcone('GoBegin', false)
  this.montreIcone('StepPrev', false)
  this.montreIcone('Pause', false)
  this.montreIcone('Play', false)
  this.montreIcone('Restart', false)
  this.montreIcone('StepNext', false)
  this.montreIcone('GoEnd', false)
  this.iconPlayGris.setAttribute('visibility', 'hidden')
  this.iconPlay2.setAttribute('visibility', 'visible')
  this.montreIcone('XML', true)
}
/**
 * Fonction dsactivant toutes les icnes suf l'icne de pause
 */
IepDoc.prototype.InitialiseIcones = function () {
  this.montreIcone('GoBegin', false)
  this.montreIcone('StepPrev', false)
  this.montreIcone('Pause', true)
  this.montreIcone('Play', false)
  this.montreIcone('Restart', false)
  this.montreIcone('StepNext', false)
  this.montreIcone('GoEnd', false)
  this.montreIcone('XML', false)
}
/**
 * Fonction appele lors d'un clic sur l'icne de retour de la figure au dbut
 */
IepDoc.prototype.onGoBegin = function () {
  this.animationEnCours = false
  this.initialise()
  this.montreIcone('GoBegin', false)
  this.montreIcone('StepPrev', false)
  this.montreIcone('Pause', false)
  this.montreIcone('Play', true)
  this.montreIcone('Restart', false)
  this.montreIcone('StepNext', true)
  this.montreIcone('GoEnd', true)
  this.montreIcone('XML', true)
  this.indiceActionEnCours = 0
}
/**
 * Fonction appele lors d'un clic sur l'icne de saut   la fin de la figure
 */
IepDoc.prototype.onGoEnd = function () {
  this.animationEnCours = false
  this.initialise()
  this.montreIcone('GoBegin', true)
  this.montreIcone('StepPrev', true)
  this.montreIcone('Pause', false)
  this.montreIcone('Play', false)
  this.montreIcone('Restart', true)
  this.montreIcone('StepNext', false)
  this.montreIcone('GoEnd', false)
  this.montreIcone('XML', true)
  this.executeJusque(this.actions.length - 1)
  this.indiceActionEnCours = this.actions.length
}
/**
 * Fonction appele quand on clique sur l'icne play de la figure
 */
IepDoc.prototype.onPlay = function () {
  this.animationEnCours = true
  this.montreIcone('GoBegin', false)
  this.montreIcone('StepPrev', false)
  this.montreIcone('Pause', true)
  this.montreIcone('Play', false)
  this.montreIcone('Restart', false)
  this.montreIcone('StepNext', false)
  this.montreIcone('GoEnd', false)
  this.montreIcone('XML', false)
  this.actions[this.indiceActionEnCours].execute(false)
}
/**
 * Fonction appele quand on clique sur l'icne continuer de la figure
 */

IepDoc.prototype.onPlayContinuer = function () {
  if (this.indiceActionEnCours === this.actions.length) {
    this.onGoEnd()
    return
  }
  this.iconPlay2.setAttribute('visibility', 'hidden')
  this.montreIcone('GoBegin', false)
  this.montreIcone('StepPrev', false)
  this.montreIcone('Pause', true)
  this.montreIcone('Play', false)
  this.montreIcone('Restart', false)
  this.montreIcone('StepNext', false)
  this.montreIcone('GoEnd', false)
  this.montreIcone('XML', false)
  this.animationEnCours = true
  this.indiceActionEnCours++
  this.actions[this.indiceActionEnCours].execute(false)
}
/**
 * Fonction appele quand on clique sur l'icne de retour au dbut et jouer
 */

IepDoc.prototype.onRestart = function () {
  const t = this
  if (this.animationEnCours) {
    this.animationEnCours = false
    setTimeout(function () {
      t.onRestart()
    }, 100)
    return
  }
  if (this.animationEnCours) return
  this.animationEnCours = false
  this.initialise()
  this.indiceActionEnCours = 0
  this.onPlay()
}
/**
 * Fonction appele quand on clique sur l'icne pause de la figure
 */

IepDoc.prototype.onPause = function () {
  const t = this
  t.animationEnCours = false
  // On laisse 5 diximes de seconde pour finir l'animation en cours.
  setTimeout(function () {
    t.montreIcone('GoBegin', true)
    t.montreIcone('StepPrev', true)
    t.montreIcone('Pause', false)
    t.montreIcone('Play', true)
    t.montreIcone('Restart', true)
    t.montreIcone('StepNext', true)
    t.montreIcone('GoEnd', true)
    t.montreIcone('XML', true)
  }, 500)
}
/**
 * Fonction appele quand on clique sur l'icne suivant de la figure (pas   pas)
 */
IepDoc.prototype.onStepNext = function () {
  const nbActions = this.actions.length
  this.animationEnCours = false
  if (this.indiceActionEnCours < nbActions) {
    if (this.actions[this.indiceActionEnCours].actionVisible()) this.indiceActionEnCours++
    else this.indiceActionEnCours = this.indiceProchaineActionVisible(this.indiceActionEnCours) + 1
    this.executeJusque(this.indiceActionEnCours - 1)
  }
  const derniereAction = this.indiceActionEnCours === this.actions.length
  this.montreIcone('GoBegin', true)
  this.montreIcone('StepPrev', true)
  this.montreIcone('Pause', false)
  this.montreIcone('Play', !derniereAction)
  this.montreIcone('Restart', true)
  this.montreIcone('StepNext', !derniereAction)
  this.montreIcone('GoEnd', !derniereAction)
  this.montreIcone('XML', true)
}
/**
 * Fonction appele quand on clique sur l'icne prcdent de la figure (pas   pas)
 */
IepDoc.prototype.onStepPrev = function () {
  this.animationEnCours = false
  // this.indiceActionEnCours--;
  this.indiceActionEnCours = this.indicePrecedenteActionVisible(this.indiceActionEnCours - 1)
  if (this.indiceActionEnCours > 0) this.executeJusque(this.indiceActionEnCours - 1)
  const premiereAction = this.indiceActionEnCours === 0
  this.montreIcone('GoBegin', !premiereAction)
  this.montreIcone('StepPrev', !premiereAction)
  this.montreIcone('Pause', false)
  this.montreIcone('Play', true)
  this.montreIcone('Restart', false)
  this.montreIcone('StepNext', true)
  this.montreIcone('GoEnd', true)
  this.montreIcone('XML', true)
}

/**
 * Fonction cachant tous les lments graphiques de la figure, rinitialisant tous les outils
 * et rinitialisant la position de tous les objets ou instruments qui peuvent tre translats, tourns ou zooms
 *   savoir les points et images
 */
IepDoc.prototype.initialise = function () {
  let i, el
  const svg = this.svg
  for (i = 0; i < svg.childNodes.length; i++) {
    el = svg.childNodes[i]
    if (el !== this.barreIcones) {
      try { // Pour compatibilit avec explorer
        svg.childNodes[i].setAttribute('visibility', 'hidden')
      } catch (e) {}
    }
  }
  for (i = 0; i < this.actions.length; i++) {
    const action = this.actions[i]
    if (action instanceof ActionCreation) action.objet.initialisePosition()
  }
  this.initialiseOutils()
}
/**
 * Fonction donnant l'tat de visibilit de l'instrument instrument aprs l'excution
 * de l'action d'indice indaction
 * @param {iep.instrumentAncetre} instrument
 * @param {Integer} indaction
 * @returns {boolean}
 */
IepDoc.prototype.getInstrumentVisibility = function (instrument, indaction) {
  if (indaction === -1) return false
  let action
  let visible = false
  for (let i = 0; i <= indaction; i++) {
    action = this.actions[i]
    if (action instanceof ActionMontrerInstrument) {
      if (action.instrument === instrument) visible = true
    } else if (action instanceof ActionMasquerInstrument) {
      if (action.instrument === instrument) visible = false
    }
  }
  return visible
}
/**
 * Fonction donnant l'tat de visibilit de l'objet objet aprs l'excution
 * de l'action d'indice indaction
 * @param {iep.ObjetBase} objet
 * @param {Integer} indaction
 * @returns {boolean}
 */
IepDoc.prototype.getObjectVisibility = function (object, indaction) {
  if (indaction === -1) return false
  let action
  let visible = false
  for (let i = 0; i <= indaction; i++) {
    action = this.actions[i]
    if (action instanceof ActionMontrer) {
      if (action.objet === object) visible = true
    } else if (action instanceof ActionMasquer) {
      if (action.objet === object) visible = false
    }
  }
  return visible
}
/**
 * Fonction donnant l'tat du compas (lev ou couch) aprs l'excution
 * de l'action d'indice indaction.
 * Si le compas est couch, renvoie "couche", s'il est leve, renvoie "leve"
 * @param {Integer} indaction
 * @returns {string}
 */
IepDoc.prototype.getCompasStatus = function (indaction) {
  if (indaction === -1) return 'couche'
  let couche = true
  let action
  for (let i = 0; i <= indaction; i++) {
    action = this.actions[i]
    if (action instanceof ActionCoucherCompas) {
      couche = true
    } else if (action instanceof ActionLeverCompas) {
      couche = false
    }
  }
  return couche ? 'couche' : 'leve'
}
/**
 * Fonction renvoyant l'indice de la prochaine action qui aura une action visible sur la figure
 *   partir de l'indice ind
 * Si aucune action visible ne figure aprs ind, renvoie this.actions.length
 * @param {type} ind
 * @returns {iep.iepDoc.prototype.indiceProchaineActionVisible.i|iep.iepDoc.actions.length}
 */
IepDoc.prototype.indiceProchaineActionVisible = function (ind) {
  if (ind >= this.actions.length) return this.actions.length - 1
  for (let i = ind; i < this.actions.length; i++) {
    if (this.actions[i].actionVisible()) return i
  }
  return this.actions.length - 1
}
/**
 * Fonction renvoyant l'indice de la prcdene action qui aura une action visible sur la figure
 *   partir de l'indice ind
 * Si aucune action visible ne figure avant ind, renvoie 0
 * @param {type} ind
 * @returns {iep.iepDoc.prototype.indiceProchaineActionVisible.i|iep.iepDoc.actions.length}
 */

IepDoc.prototype.indicePrecedenteActionVisible = function (ind) {
  if (ind < 0) return 0
  if (ind === this.actions.length) ind--
  for (let i = ind; i >= 0; i--) {
    if (this.actions[i].actionVisible()) return i
  }
  return 0
}
/**
 * Fonction montrant les instruments   leur tape de visibilit   l'tape n
 * @param {Integer} n : l'indice de la dernire action excute
 */
IepDoc.prototype.montreInstrumentsEtape = function (n) {
  let visible = this.getInstrumentVisibility(this.compas, n)
  if (this.compas.leve) this.compasLeve.montre(visible)
  else this.compas.montre(visible)
  visible = this.getInstrumentVisibility(this.crayon, n)
  this.crayon.montre(visible)
  visible = this.getInstrumentVisibility(this.equerre, n)
  this.equerre.montre(visible)
  visible = this.getInstrumentVisibility(this.rapporteur, n)
  this.rapporteur.montre(visible)
  visible = this.getInstrumentVisibility(this.regle, n)
  this.regle.montre(visible)
  visible = this.getInstrumentVisibility(this.requerre, n)
  this.requerre.montre(visible)
}
/**
 * Fonction exccutant sans animation toutes les actions de la figure jusqu'  l'indice indfin compris
 * @param {Integer} indfin
 */
IepDoc.prototype.executeJusque = function (indfin) {
  if (indfin >= this.actions.length) return
  this.initialise()
  this.initialiseOutils()
  this.animationEnCours = false
  for (let i = 0; i <= indfin; i++) { this.actions[i].execute(true) } // true pour que l'excution soit immdiate
  this.montreInstrumentsEtape(indfin)
}
/**
 * Fonction recherchant s'il y a dj  dans la liste doc.elements un lment de mme id et de mme
 * nature.
 * Si oui on remplace l'ancien. Sinon on rajoute element   la liste.
 * @param {iep.doc.elements[]} element
 */
IepDoc.prototype.addElement = function (element) {
  let i, el
  for (i = 0; i < this.elements.length; i++) {
    el = this.elements[i]
    // S'il existe un lment de mme type et de mme id, on le remplace par le nouveau
    if ((el.id === element.id) && (el.objet === element.objet)) {
      this.elements[i] = element
      return
    }
  }
  // S'il n'y a pas d'lment du mme type et de mme id, on ajoute le nouvel lment
  this.elements.push(element)
}
/**
 * Fonction appele dans le cas o on est sur que la liste doc.elements ne contient pas
 * dj  un lment de mme id et de mme nature que element
 * @param {type} element
 */
IepDoc.prototype.pushElement = function (element) {
  this.elements.push(element)
}
/**
 * Fonction renvoyant l'objet de elements d'id gale   id s'il y en a une et sinon null
 * @param {type} id
 * @returns {Boolean|iep.iepDoc.elements|iep.iepDoc.prototype.element.el}
 * Attention : Le quadrillage est le seul objet a ne pas avoir d'id et il ne peut y en avoir qu'un par figure
 */
IepDoc.prototype.getElement = function (id, nature) {
  let i, el
  if (nature === 'quadrillage') {
    for (i = 0; i < this.elements.length; i++) {
      el = this.elements[i]
      if (el.objet === nature) return el
    }
  } else {
    for (i = 0; i < this.elements.length; i++) {
      el = this.elements[i]
      if ((el.id === id) && (el.objet === nature)) return el
    }
  }
  return null
}
/**
 * Fonction remplaant l'lment de elements did id par e s'il y en a un
 * S'il n'y a pas dlment d'id id renvoie false
 * @param {type} id
 * @param {type} e
 * @returns {boolean}
 */
IepDoc.prototype.setElement = function (id, e) {
  for (let i = 0; i < this.elements.length; i++) {
    if (this.elements[i].id === id) {
      this.elements[i] = e
      return true
    }
  }
  return false
}
/**
 * Fonction ajoutant l'action action   la liste d'actions listeActions
 * @param {type} action
 */
IepDoc.prototype.ajouteAction = function (action) {
  // On donne   action un indice por qu'lle sache quelle est son rand dans la liste
  // Ainsi elle pourra ensuite indiquer si son action sur la figure est visible ou non
  action.indice = this.actions.length
  this.actions.push(action)
}
/**
 * Fonction crant toutes les actions de la figure
 * @param {type} ind
 */
IepDoc.prototype.creeActions = function () {
  let ind, el, at, ato, objet, action, forme, xcrayon, ycrayon, id,
    angle, sens, vitesse, abs, ord, cibles, coord, tempo, len, ecart, v1, echelle, point, cible
  for (ind = 0; ind < this.tabact.length; ind++) {
    el = this.tabact[ind]
    id = this.getId(el)
    at = el.getAttribute('mouvement')
    tempo = el.getAttribute('tempo')
    try {
      let abscisse_bas_droite, ecartAngle // faut viter de dclarer dans un case directement, on le fait dans le bloc parent (eslint no-case-declarations)
      switch (at) {
        case 'creer':
          ato = el.getAttribute('objet')
          // Si on a dj  cr un objet avec le mme id c'est une erreur et on saute a cration
          if (this.getElement(id, ato) !== null) break

          if (ato != null) {
            switch (ato) {
              case 'point':
                objet = new Point(this, id, parseFloat(this.getAbs(el)), parseFloat(this.getOrd(el)),
                  this.getCouleur(el), this.getEpaisseur(el))
                action = new ActionCreation(this, id, objet, tempo)
                break

              case 'angle':
                objet = new Angle(this, id, this.getAbs(el), this.getOrd(el), el.getAttribute('rayon'),
                  el.getAttribute('angle1'), el.getAttribute('angle2'), this.getCouleur(el), this.getEpaisseur(el),
                  this.getOpaciteFond(el), el.getAttribute('forme'))
                action = new ActionCreation(this, id, objet, tempo)
                break

              case 'angle_droit':
                objet = new AngleDroit(this, id, el.getAttribute('abscisse_sommet'), el.getAttribute('ordonnee_sommet'),
                  el.getAttribute('abscisse_inter'), el.getAttribute('ordonnee_inter'), this.getCouleur(el), this.getEpaisseur(el))
                action = new ActionCreation(this, id, objet, tempo)
                break

              case 'longueur':
                objet = new MarqueSegment(this, id, this.getAbs(el), this.getOrd(el),
                  this.getCouleur(el), this.getEpaisseur(el),
                  el.getAttribute('forme'))
                action = new ActionCreation(this, id, objet, tempo)
                break

              case 'texte':
                objet = new Texte(this, id, this.getAbs(el), this.getOrd(el), this.getCouleur(el), el.getAttribute('taille'))
                action = new ActionCreation(this, id, objet, tempo)
                break

              case 'repere':
                objet = new Repere(this, el.getAttribute('hauteur'), el.getAttribute('largeur'),
                  el.getAttribute('haut'), el.getAttribute('gauche'), el.getAttribute('Xgrad'),
                  el.getAttribute('Ygrad'), el.getAttribute('Xmin'), el.getAttribute('Xmax'),
                  el.getAttribute('Ymin'), el.getAttribute('Ymax'), this.getCouleur(el),
                  el.getAttribute('grille'), el.getAttribute('axes'), el.getAttribute('etiquettes'))
                action = new ActionCreation(this, id, objet, tempo)
                break

              case 'quadrillage':
                abscisse_bas_droite = el.getAttribute('abscisse_bas_droite')
                if (abscisse_bas_droite != null) { // Syntaxe non documente dans la doc
                  abscisse_bas_droite = parseFloat(abscisse_bas_droite) / 30
                  const ordonnee_bas_droite = parseFloat(el.getAttribute('ordonnee_bas_droite')) / 30
                  const abscisse_haut_gauche = parseFloat(el.getAttribute('abscisse_haut_gauche')) / 30
                  const ordonnee_haut_gauche = parseFloat(el.getAttribute('ordonnee_haut_gauche')) / 30
                  objet = new Quadrillage(
                    this,
                    el.getAttribute('quadrillage'),
                    String(ordonnee_bas_droite - ordonnee_haut_gauche),
                    String(abscisse_bas_droite - abscisse_haut_gauche),
                    ordonnee_haut_gauche,
                    abscisse_haut_gauche,
                    this.getCouleur(el)
                  )
                  action = new ActionCreation(this, 'quadrillageIEP', objet, tempo)
                } else {
                  objet = new Quadrillage(this, el.getAttribute('quadrillage'), el.getAttribute('hauteur'),
                    el.getAttribute('largeur'), el.getAttribute('haut'), el.getAttribute('gauche'), this.getCouleur(el))
                }
                action = new ActionCreation(this, 'quadrillageIEP', objet, tempo)
                break

              case 'axe':
                objet = new Axe(this, el.getAttribute('pente'), el.getAttribute('largeur'),
                  el.getAttribute('haut'), el.getAttribute('gauche'), el.getAttribute('distanceBord'),
                  el.getAttribute('Xgrad'), el.getAttribute('Xmin'), el.getAttribute('Xmax'), this.getCouleur(el))
                action = new ActionCreation(this, id, objet, tempo)
                break

              case 'gabarit':
                ecartAngle = el.getAttribute('ecart_angle')
                if (ecartAngle === null) ecartAngle = '-30'
                objet = new Gabarit(this, id, el.getAttribute('abscisse'), el.getAttribute('ordonnee'), el.getAttribute('rayon'),
                  ecartAngle, this.getCouleur(el), this.getEpaisseur(el), this.getCouleurFond(el), this.getOpaciteTrait(el))
                action = new ActionCreation(this, id, objet, tempo)
                break

              default:
                action = null // Pour ce que j'aurais oubli d'implmenter
            }

            if (action) {
              this.ajouteAction(action)
              action.setReady()
            }
          }
          break
        case 'montrer':
          ato = el.getAttribute('objet')
          if (kernel.estInstrument(ato)) {
            abs = el.getAttribute('abscisse')
            ord = el.getAttribute('ordonnee')
            action = new ActionMontrerInstrument(this, this[ato], abs, ord, tempo)
            if (((ato === 'crayon') || (ato === 'compas')) && (abs != null) && (ord != null)) this[ato].translate(parseFloat(abs), parseFloat(ord))
          } else action = new ActionMontrer(this, id, ato, tempo)
          this.ajouteAction(action)
          action.setReady()
          break
        case 'masquer':
          ato = el.getAttribute('objet')
          if (kernel.estInstrument(ato)) action = new ActionMasquerInstrument(this, this[ato], tempo)
          else action = new ActionMasquer(this, id, ato, tempo)
          this.ajouteAction(action)
          action.setReady()
          break
        case 'tracer':
          ato = el.getAttribute('objet')
          cible = el.getAttribute('cible')
          if (cible != null) {
            objet = this.getElement(cible, 'point') // Le point vers lequel on veut translater l'objet
            abs = objet.xcons
            ord = objet.ycons
          } else {
            abs = parseFloat(this.getAbs(el))
            ord = parseFloat(this.getOrd(el))
          }
          switch (ato) {
            case 'crayon':
              forme = el.getAttribute('forme')
              xcrayon = this.crayon.x
              ycrayon = this.crayon.y
              id = this.getId(el)
              switch (forme) {
                case null: // Cas d'un segment
                  if (isNaN(abs) || isNaN(ord)) { // Cas d'une forme libre pas dclare
                    objet = new LigneContinue(this, id, this.crayon.x, this.crayon.y, el.getAttribute('abscisses'),
                      el.getAttribute('ordonnees'), this.getCouleur(el), this.getEpaisseur(el), this.getOpaciteTrait(el),
                      this.getStyleTrait(el), this.getStyle(el))
                    len = objet.abs.length
                    this.crayon.translate(objet.abs[len - 1], objet.ord[len - 1])
                  } else {
                    objet = new Segment(this, id, xcrayon, ycrayon, abs, ord, this.getCouleur(el),
                      this.getEpaisseur(el), this.getOpaciteTrait(el), this.getStyleTrait(el), this.getStyle(el))
                    this.crayon.translate(abs, ord)
                  }
                  break
                case 'droite': // Cas d'une droite
                  if (isNaN(abs) || isNaN(ord)) break
                  objet = new Droite(this, id, xcrayon, ycrayon, abs, ord, this.getCouleur(el),
                    this.getEpaisseur(el), this.getOpaciteTrait(el), this.getStyleTrait(el), this.getStyle(el))
                  this.crayon.translate(abs, ord)
                  break
                case 'demidroite': // Cas d'une demi-droite
                  if (isNaN(abs) || isNaN(ord)) break
                  objet = new DemiDroite(this, id, xcrayon, ycrayon, abs, ord, this.getCouleur(el),
                    this.getEpaisseur(el), this.getOpaciteTrait(el), this.getStyleTrait(el), this.getStyle(el))
                  this.crayon.translate(abs, ord)
                  break
                case 'polygone':
                  cibles = el.getAttribute('cibles')
                  if (cibles === null) {
                    objet = new Polygone(this, id, el.getAttribute('abscisses'),
                      el.getAttribute('ordonnees'), this.getCouleur(el), this.getEpaisseur(el),
                      this.getCouleurFond(el), el.getAttribute('opacite'))
                  } else {
                    coord = this.getCoord(cibles)
                    objet = new Polygone(this, id, coord.abs, coord.ord,
                      this.getCouleur(el), this.getEpaisseur(el),
                      this.getCouleurFond(el), el.getAttribute('opacite'))
                  }
                  len = objet.abs.length
                  this.crayon.translate(objet.abs[len - 1], objet.ord[len - 1])
                  break
                case 'libre':
                  objet = new LigneContinue(this, id, this.crayon.x, this.crayon.y, el.getAttribute('abscisses'),
                    el.getAttribute('ordonnees'), this.getCouleur(el), this.getEpaisseur(el), this.getOpaciteTrait(el),
                    this.getStyleTrait(el), this.getStyle(el))
                  len = objet.abs.length
                  this.crayon.translate(objet.abs[len - 1], objet.ord[len - 1])
              }
              vitesse = el.getAttribute('vitesse')
              if (vitesse == null) vitesse = '4'
              action = new ActionCreation(this, this.getId(el), objet, tempo, vitesse)
              this.ajouteAction(action)
              action.setReady()
              break
            case 'trait':
              vitesse = el.getAttribute('vitesse')
              if (vitesse === null) vitesse = 10000 // Instantan si vitesse pas prcis
              objet = new Segment(this, id, el.getAttribute('abscisse1'), el.getAttribute('ordonnee1'),
                el.getAttribute('abscisse2'), el.getAttribute('ordonnee2'), this.getCouleur(el),
                this.getEpaisseur(el), this.getOpaciteTrait(el), this.getStyleTrait(el), this.getStyle(el))
              action = new ActionCreation(this, this.getId(el), objet, tempo, vitesse) // Pas d'animation pour les traits
              this.ajouteAction(action)
              action.setReady()
              break
            case 'compas':
              objet = new Arc(this, id, this.compas.x, this.compas.y, this.compas.ecart,
                this.getAngle(el, 'debut'), this.getAngle(el, 'fin'), this.getCouleur(el), this.getEpaisseur(el), this.getOpaciteTrait(el),
                this.getStyleTrait(el))
              vitesse = el.getAttribute('sens')
              if (vitesse == null) vitesse = '8'
              action = new ActionCreation(this, this.getId(el), objet, tempo, vitesse)
              this.ajouteAction(action)
              action.setReady()
              break
            case 'angle_droit': // Certains scripts utilisent tracer au lieu de crer pour les angles droits ...
              objet = new AngleDroit(this, id, el.getAttribute('abscisse_sommet'), el.getAttribute('ordonnee_sommet'),
                el.getAttribute('abscisse_inter'), el.getAttribute('ordonnee_inter'), this.getCouleur(el), this.getEpaisseur(el))
              action = new ActionCreation(this, this.getId(el), objet, tempo, vitesse)
              this.ajouteAction(action)
              action.setReady()
              break
          }
          break
        case 'rotation':
          ato = el.getAttribute('objet')
          sens = el.getAttribute('sens')
          if (kernel.estInstrument(ato)) {
            cible = el.getAttribute('cible')
            // Au cas ou un auteur a mis   la fois un attribut cible et un attribut angle,
            // C'est l'attribut angle qui semble l'emporter (constat par des tests).
            if ((cible != null) && (el.getAttribute('angle') === null)) {
              objet = this.getElement(cible, 'point') // Le point vers lequel on veut translater l'objet
              abs = objet.xcons
              ord = objet.ycons
              v1 = new Vect(this[ato].x, this[ato].y, abs, ord)
              if (v1.presqueNul()) angle = 0
              else angle = -v1.angle()
            } else {
              if (ato === 'compas') angle = this.getAngle(el, 'angle')
              else angle = el.getAttribute('angle')
            }
            action = new ActionRotationInstrument(this, this[ato], angle, tempo, sens)
          } else {
            // On vrifie que l'action est cohrente (que l'objet cible existe)
            if (this.getElement(id, ato) === null) action = null
            else {
              angle = el.getAttribute('angle')
              action = new ActionRotationObjet(this, id, angle, ato, tempo, sens)
            }
          }
          if (action !== null) {
            this.ajouteAction(action)
            action.setReady()
          }
          break
        case 'translation':
          ato = el.getAttribute('objet')
          cible = el.getAttribute('cible')
          // Au cas o on a spcifi abscisse et ordonne et cible, cible aa priorit
          if (cible != null) {
            objet = this.getElement(cible, 'point') // Le point vers lequel on veut translater l'objet
            abs = objet.xcons
            ord = objet.ycons
          } else {
            abs = parseFloat(this.getAbs(el))
            ord = parseFloat(this.getOrd(el))
          }
          vitesse = el.getAttribute('vitesse')
          if (kernel.estInstrument(ato)) {
            this[ato].x = abs
            this[ato].y = ord
            action = new ActionTranslationInstrument(this, this[ato], abs, ord, tempo, vitesse)
          } else {
            // On vrifie que l'action est cohrente
            if (this.getElement(id, ato) === null) action = null
            else {
              // Dans le cas o on translate un point il faut modifier ses champs xcons et ycons
              // pour que tout objet ultrieur l'utilisant comme cible soit correctement initialis
              if (ato === 'point') {
                point = this.getElement(id, 'point')
                point.xcons = abs
                point.ycons = ord
              }
              action = new ActionTranslationObjet(this, id, abs, ord, ato, tempo, vitesse)
            }
          }
          if (action !== null) {
            this.ajouteAction(action)
            if ((ato === 'crayon') || (ato === 'compas')) { this[ato].translate(abs, ord) }
            action.setReady()
          }
          break
        case 'zoom':
          ato = el.getAttribute('objet')
          vitesse = el.getAttribute('vitesse')
          if (kernel.estInstrument(ato)) {
            echelle = el.getAttribute('echelle')
            this[ato].zoomfactor = parseFloat(echelle) / 100
            action = new ActionZoomInstrument(this, this[ato], echelle, tempo, vitesse)
          } else {
            // On ne peut zoomer que sur un texte ou une image
            if ((ato === 'texte' || (ato === 'image'))) action = new ActionZoomObjet(this, id, el.getAttribute('echelle'), ato, tempo, vitesse)
            else action = null
          }
          if (action !== null) {
            this.ajouteAction(action)
            action.setReady()
          }
          break
        case 'ecarter':
          cible = el.getAttribute('cible')
          if (cible != null) {
            objet = this.getElement(cible, 'point') // Le point vers lequel on veut translater l'objet
            v1 = new Vect(this.compas.x, this.compas.y, objet.xcons, objet.ycons)
            ecart = v1.norme()
          } else ecart = parseFloat(el.getAttribute('ecart')) * this.compas.zoomfactor
          this.compas.ecart = ecart
          action = new ActionEcarterCompas(this, ecart, tempo, el.getAttribute('vitesse'))
          this.ajouteAction(action)
          action.setReady()
          break
        case 'lever':
          action = new ActionLeverCompas(this, tempo)
          this.ajouteAction(action)
          action.setReady()
          break
        case 'coucher':
          action = new ActionCoucherCompas(this, tempo)
          this.ajouteAction(action)
          action.setReady()
          break

        case 'masquer_nombres':
          action = new ActionMontrerNombres(this, false, tempo)
          this.ajouteAction(action)
          action.setReady()
          break
        case 'montrer_nombres':
          action = new ActionMontrerNombres(this, true, tempo)
          this.ajouteAction(action)
          action.setReady()
          break
        case 'graduations':
          ato = el.getAttribute('objet')
          action = new ActionMontrerGraduations(this, this[ato], true, tempo)
          this.ajouteAction(action)
          action.setReady()
          break
        case 'vide':
          ato = el.getAttribute('objet')
          action = new ActionMontrerGraduations(this, this[ato], false, tempo)
          this.ajouteAction(action)
          action.setReady()
          break
        case 'retourner':
          this.compasRetourne = !this.compasRetourne
          action = new ActionRetourner(this, tempo)
          this.ajouteAction(action)
          action.setReady()
          break
        case 'glisser':
          action = new ActionGlisser(this, el.getAttribute('abscisse'), tempo, el.getAttribute('vitesse'))
          this.ajouteAction(action)
          action.setReady()
          break
        case 'chargement':
          // Attention, pour l'image, pas d'appel de creeAction(ind+1)
          // car c'est une fonction de callBack qui s'en charge une fois l'image charge
          objet = new Imag(this, id, el.getAttribute('url'))
          objet.prepareAction()
          // Pas de action.setReady car il faut attendre que la figure soit charge
          break
        case 'ecrire': // Texte
          action = new ActionEcrireTexte(this, id, this.getCouleur(el), el.getAttribute('taille'),
            el.getAttribute('texte'), el.getAttribute('style'), this.getCouleurFondTexte(el),
            el.getAttribute('opacite_fond'),
            this.getCouleurCadre(el), el.getAttribute('epaisseur_cadre'),
            el.getAttribute('marge'), el.getAttribute('marge_gauche'),
            el.getAttribute('marge_droite'), el.getAttribute('marge_haut'),
            el.getAttribute('marge_bas'), tempo)
          this.ajouteAction(action)
          action.prepare()
          break
        case 'nommer':
          // on regarde si le point   nommer a t cr sinon erreur de script
          if (this.getElement(id, 'point') !== null) {
            // Dans certains script on nommait un point avec une chane vide ce qui n'a pas de sens.
            const nomPoint = el.getAttribute('nom')
            if (nomPoint !== '') {
              objet = new NomPoint(this, id, this.getAbs(el), this.getOrd(el),
                nomPoint, this.getCouleur(el))
              action = new ActionNommerPoint(this, objet, tempo)
              this.ajouteAction(action)
              action.prepare()
            }
          }
          break
        case 'pause':
          action = new ActionPause(this, tempo)
          this.ajouteAction(action)
          action.setReady()
          break
        case 'modifier_longueur':
          ato = el.getAttribute('objet')
          action = new ActionModifierLongueur(this, this[ato], el.getAttribute('longueur'), tempo)
          this.ajouteAction(action)
          action.setReady()
          break
          // Modification du rayon d'un gabarit
        case 'modifier_rayon':
          ato = el.getAttribute('objet')
          action = new ActionModifierRayon(this, id, el.getAttribute('rayon'), ato, tempo)
          this.ajouteAction(action)
          action.setReady()
          break
      }
    } catch (e) {
      // On ne fait rien si xml incorrect
    }
  }
  // Une fois les actions cres on attend que les lments asynchrones soient prts pour continuer
  // en laissant 10 secondes maxi pour le chargement de la figure
  const self = this
  setTimeout(function () {
    if (!self.started) {
      console.error('10s aprs linstanciation IepDoc la figure nest toujours pas prte')
      window.alert('Le chargement de la figure IEP a chou')
      self.started = true
    }
  }, 20000) // On laisse maxi 10 secondes   la figure pour se charger

  this.waitForReadyState()
}
/**
 * Si immediat est true, les actions sont excutes immdiatement, sans animation et sans temporisation
 * @param {type} immediat
 */
IepDoc.prototype.execute = function (immediat) {
  this.indiceActionEnCours = 0
  this.actions[0].execute(immediat)
}
/**
 * Fonction appele lorsqu'une action a t excute pour passer   l'excution suivante.
 * Si immediat est true, on retourne sans rien faire
 * Si animationECours en false, on revient sans rien faire (pour arrter une animation en cours)
 * Si l'action.tempo n'est pas nul, on attend action.tempo millimes de seconde avant de passer   l'action suivante
 * indiceActionEnCours est l'indice de la prochaine action   excuter
 * @param {boolean} immediat
 */
IepDoc.prototype.actionSuivante = function (immediat) {
  const imm = (arguments.length === 0) ? false : immediat
  if (!this.animationEnCours && !imm) {
    // if (this.indiceActionEnCours != this.actions.length-1) this.indiceActionEnCours++;
    return
  } // Pas de passage   l'action suivante pour le mode pas   pas.
  const action = this.actions[this.indiceActionEnCours]
  if ((action.tempo === null) || imm) this.passageActionSuivante(imm)
  else {
    const t = this
    setTimeout(function () { t.passageActionSuivante(imm) }, action.tempo)
  }
}
/**
 * Fonction appele par actionSuivante pour passer   l'action suivante
 * indiceActionEnCours est l'indice de la prochaine action   excuter
 * @param {boolean} immediat
 */
IepDoc.prototype.passageActionSuivante = function (immediat) {
  // IL peut arriver qu'on arrive ici alors qu'on a cliqu pour arrter une animation, cliqu rapidement
  // pour aller   la fin alors que l'animation n'tait pas finie...
  if (this.indiceActionEnCours >= this.actions.length) return
  if (this.indiceActionEnCours !== this.actions.length - 1) {
    this.indiceActionEnCours++
    this.actions[this.indiceActionEnCours].execute(immediat)
  } else {
    this.indiceActionEnCours++
    this.montreIcone('GoBegin', true)
    this.montreIcone('StepPrev', true)
    this.montreIcone('Pause', false)
    this.montreIcone('Play', false)
    this.montreIcone('StepNext', false)
    this.montreIcone('GoEnd', false)
    this.montreIcone('Restart', true)
    this.montreIcone('XML', true)
    this.animationEnCours = false
  }
}
