const ActionAncetre = require('./actionAncetre')
const mathjaxLoader = require('../mathjaxLoader')

module.exports = ActionNommerPoint

/**
 * Action donnant un nom   un point
 * @extends iep.actionAncetre
 * @constructor
 * @param {ie.iepDoc} doc : le doculent propritaire
 * @param {iep.nomPoint} objet :  l'objet reprsentant le nom
 * @param {string} tempo : le tempk en diximes de seconde (ou null)
 */
function ActionNommerPoint (doc, objet, tempo) {
  ActionAncetre.call(this, doc, tempo)
  this.nomPoint = objet
}
ActionNommerPoint.prototype = new ActionAncetre()

/**
 * Appele par creeActions, prcharge Mathjax si besoin et prpare son div de rendu
 */
ActionNommerPoint.prototype.prepare = function () {
  if (this.nomPoint.estLatex) {
    this.nomPoint.div = document.createElement('div')
    // On diminue la taille pour le LaTeX eet on ne spcifie pas la taille en px
    // diffrence par rapport   MathGraph32
    this.nomPoint.div.setAttribute('style', 'top:0px;left:0px;position:absolute;font-size:' +
      String(this.nomPoint.taille - 2) + 'px;visibility:hidden;')
    this.nomPoint.div.setAttribute('id', this.nomPoint.id + 'iepprov')
    const ch = '$$' + this.nomPoint.nom.substring(1) + '$'
    this.nomPoint.div.appendChild(document.createTextNode(ch))
    document.body.appendChild(this.nomPoint.div)
    // la suite est async
    const self = this
    mathjaxLoader().then(() => {
      /* global MathJax */
      // MathJax.Hub.Typeset(this.div);
      MathJax.Hub.Queue(['Typeset', MathJax.Hub, self.nomPoint.div])
      MathJax.Hub.Queue([function () {
        self.nomPoint.creegLatex()
        self.setReady()
      }])
    }).catch(error => {
      console.error(Error('Mathjax na pas t charg correctement, impossible dafficher le point en LaTeX'), error)
    })
  } else {
    this.nomPoint.creeg()
    this.setReady()
  }
}

/**
 * Excutant de l'action donnant un nom au point
 * @param {boolean} immediat : Si true pas de passage   l'action suivante
 */
ActionNommerPoint.prototype.execute = function (immediat) {
  // if ((this.nomPoint.objet === "point") && !this.nomPoint.nomCree) {
  if (this.nomPoint.objet === 'point') {
    // this.nomPoint.creeg();
    this.nomPoint.positionne()
    const point = this.nomPoint.point
    if (point.nom !== null) { this.doc.svg.replaceChild(this.nomPoint.g, point.nom.g) } else this.doc.svg.appendChild(this.nomPoint.g)
    point.nom = this.nomPoint
  }
  this.nomPoint.g.setAttribute('visibility', 'visible')
  if (!immediat) this.doc.actionSuivante(immediat)
}

/** @inheritDoc */
ActionNommerPoint.prototype.actionVisible = function () {
  return this.doc.getObjectVisibility(this.nomPoint.point, this.indice - 1)
}
