'use strict'
/* eslint-env browser */
const { addJs } = require('sesajstools/dom')

const mathJaxUrl = 'https://www.mathgraph32.org/js/MathJax/MathJax.js?config=TeX-AMS-MML_SVG-full'
const configCode = `MathJax.Hub.Config({
  tex2jax: {
    inlineMath: [['$', '$'],['\\\\(', '\\\\)']]
  },
  jax: ['input/TeX','output/SVG'],
  TeX: {extensions: ['color.js']},
  messageStyle:'none'
});`
// en s
const loadDelay = 10

// La 1re promesse de chargement qui sera cre, puis renvoye lors de tous les appels
// (a permet de ne lancer qu'un seul chargement mme en cas d'appels multiples avant que MathJax ne soit compltement charg)
let firstLoadingPromise

/**
 * Charge mathjax dans le <head> de la page courante
 * (appel par le prepare de ActionEcrireTexte et ActionNommerPoint,
 * donc dclenche le chargement ds que le tableau des actions est construit,
 * seulement si l'une des actions a besoin de Mathjax)
 * @private
 * @return {Promise}
 */
module.exports = function mathjaxLoader () {
  // Au 1er appel on charge MathJax
  if (!firstLoadingPromise) {
    if (typeof MathJax === 'undefined') {
      // faut le charger
      firstLoadingPromise = new Promise((resolve, reject) => {
        const head = window.document.getElementsByTagName('head')[0]
        // la config
        const eltConfig = window.document.createElement('script')
        eltConfig.type = 'text/x-mathjax-config'
        eltConfig.appendChild(window.document.createTextNode(configCode))
        head.appendChild(eltConfig)
        // load du js
        addJs(mathJaxUrl, () => {
          clearTimeout(timeout)
          resolve()
        })
        // on ajoute un timeout de chargement
        const timeout = setTimeout(() => {
          // on reset pour que le prochain appel relance un chargement
          firstLoadingPromise = null
          reject(Error(`Mathjax non charg aprs ${loadDelay}s dattente`))
        }, loadDelay * 1000)
      })
    } else {
      // on avait dj  charg MathJax avant le 1er appel de cette fct, on le garde
      console.log('MathJax tait dj  charg, on ne fait rien')
      firstLoadingPromise = Promise.resolve()
    }
  }
  return firstLoadingPromise
}
