    // rrue car il s'agit d'un nom : kernel.getMathsForName(ch.substring(ind+3,indv),true)
    return kernel.getMathsForName(kernel.getMathsForName(ch.substring(0, ind), btexte) + '{' + kernel.getMathsForName(ch.substring(ind + 3, indv), true) + '}_{' +
      kernel.getMathsForName(ch.substring(indv + 1, indpf), false) + '}' + kernel.getMathsForName(ch.substring(indpf + 1), btexte), btexte)
