const kernel = require('../global/global')
const ObjetLigne = require('./objetLigne')
module.exports = LigneContinue
/**
 * Classe reprsentant une ligne continue de la figure
 * @extends iep.objetBase
 * @constructor
 * @param {iepDoc} doc : Le document propritaire
 * @param {Integer} id : L'id de l'objet dans le fichier XML de la figure
 * @param {string} abs : Une chane contenant les abscisses spares par des virgules
 * @param {string} ord : Une chane contenant les ordonnes spares par des virgules
 * @param{Integer} xdeb : abscisse de dbut (en fait l'abscisse actuelle du crayon)
 * @param{Integer} ydeb : ordone de dbut (en fait l'ordonne actuelle du crayon)
 * @param {string} couleur : La couleur
 * @param {string} epaisseur : L'paisseur de trait
 * @param {string} opacite : L'opacit du trait
 * @param {string} styleTrait : tiret pour avoir des pointills
 */
function LigneContinue (doc, id, xdeb, ydeb, abs, ord, couleur, epaisseur, opacite, styleTrait) {
  ObjetLigne.call(this, doc, id, couleur, epaisseur, opacite, styleTrait)
  this.xdeb = xdeb
  this.ydeb = ydeb
  this.abs = abs.split(',')
  this.ord = ord.split(',')
  this.objet = 'trait'
}
LigneContinue.prototype = new ObjetLigne()

/**
 * Fonction mettant dans this.g l'lment graphique reprsentant l'objet dans
 * le DOM du svg de la figure
 */
LigneContinue.prototype.creeg = function () {
  let p, points, i
  p = document.createElementNS(kernel.svgsn, 'polyline')
  points = this.xdeb + ' ' + this.ydeb
  for (i = 0; i < this.abs.length; i++) {
    points += ' ' + this.abs[i] + ' ' + this.ord[i]
  }
  p.setAttribute('points', points)
  p.setAttribute('style', this.style + 'fill:none;')
  p.setAttribute('visibility', 'hidden')
  // p.setAttribute("id",this.id);
  this.g = p
}
/** @inheritDoc */
LigneContinue.prototype.creationAnimee = function () {
  return true
}
/**
 * Fonction lanant l'animation de visualisation de la ligne continue
 * Utilise un timer et une fonction de callBack
 */
LigneContinue.prototype.lanceAnimation = function () {
  this.sauveabs = this.abs
  this.sauveord = this.ord
  this.abs = []
  this.ord = []
  this.ind = -1 // L'indice dans le tableau des coordonnes
  const t = this
  this.timer = setInterval(function () { t.actionPourAnimation() }, 25)
}
/**
 * Fonction de callBack appele par un timer pour l'animation du trac
 */
LigneContinue.prototype.actionPourAnimation = function () {
  this.ind++
  const cray = this.doc.crayon
  if (this.ind >= this.sauveabs.length) {
    this.abs = this.sauveabs
    this.ord = this.sauveord
    this.updateg()
    clearInterval(this.timer)
    this.finAction()
    this.doc.actionSuivante()
  } else {
    const x = this.sauveabs[this.ind]
    const y = this.sauveord[this.ind]
    this.abs.push(x)
    this.ord.push(y)
    this.updateg()
    cray.setPosition(parseFloat(x), parseFloat(y), cray.angle)
    this.updateg()
  }
}
/**
 * extends iep.objetBase.prototype.finAction
 */
LigneContinue.prototype.finAction = function () {
  const len = this.abs.length
  this.doc.crayon.translate(parseFloat(this.abs[len - 1]), parseFloat(this.ord[len - 1]))
}
