const kernel = require('../global/global')
const DroiteAncetre = require('./droiteAncetre')
const Vect = require('../types/vecteur')
module.exports = Segment
/**
 * Classe reprsentant un segment qui peut tre affubl d'une flche
 * @extends iep.objetBase
 * @constructor
 * @param {Integer} x1 : Abscisse de l'origine
 * @param {integre} y1 : Ordonne de l'origine
 * @param {Integer} x2 : Abscisse de l'extrmit
 * @param {interger} y2 : Ordonne de l'extrmit
 * @param {string} couleur : couleur
 * @param {Integer} epaisseur : paiseur du trait
 * @param {string} opacite : transparence du trait (de 0   100)
 * @param {string} styleTrait : Si "tiret" pointills sinon trait continu
 * @param {boolean} vecteur : Si true on rajoute une flcce
 */
function Segment (doc, id, x1, y1, x2, y2, couleur, epaisseur, opacite, styleTrait, stylefleche) {
  DroiteAncetre.call(this, doc, id, x1, y1, x2, y2, couleur, epaisseur, opacite, styleTrait)
  this.stylefleche = stylefleche
}
Segment.prototype = new DroiteAncetre()
/**
 * Fonction mettant dans this.g l'lment graphique reprsentant l'objet dans
 * le DOM du svg de la figure
 * Si le segment est en fait un vecteur on cre d'abord le svg lment du segment
 * et on lui rajoute une flche
 */
Segment.prototype.creeg = function () {
  DroiteAncetre.prototype.creeg.call(this)
  if (this.stylefleche === 'vecteur') {
    const longFleche = 12
    const x1 = this.x1
    const x2 = this.x2
    const y1 = this.y1
    const y2 = this.y2
    const u0 = new Vect(x2, y2, x1, y1)
    const u1 = u0.vecteurColineaire(longFleche)
    const u2 = new Vect(u1.x * kernel.cos30 - u1.y * kernel.sin30,
      u1.x * kernel.sin30 + u1.y * kernel.cos30)
    const u3 = new Vect(u1.x * kernel.cos30 + u1.y * kernel.sin30,
      -u1.x * kernel.sin30 + u1.y * kernel.cos30)
    const points = String(x2 + u2.x) + ' ' + String(y2 + u2.y) +
      ',' + x2 + ' ' + y2 + ',' + String(x2 + u3.x) + ' ' + String(y2 + u3.y)
    const g = document.createElementNS(kernel.svgsn, 'polyline')
    g.setAttribute('points', points)
    const style = 'stroke:' + this.couleur + ';stroke-width:' + this.epaisseur +
          ';stroke-opacity:' + this.opacite + ';fill:none'
    g.setAttribute('style', style)
    this.g.appendChild(g)
  }
}
