const kernel = require('../global/global')
const InstrumentAncetre = require('./instrumentAncetre')
const Equerre = require('../instruments/equerre')
module.exports = Requerre
/**
 * @constructor
 * @extends iep.instrumentAncetre
 * Classe reprsentant la rgle-equerre dans la figure InstrumenPoche
 * @param {iep.iepDo} doc : le document propritaire de la figure
 */
function Requerre (doc) {
  InstrumentAncetre.call(this, doc)
  this.x = 200
  this.y = 400
  this.angle = 0
  this.zoomfactor = 1
  this.abscisse = 0 // L'abscisse au coin de l'querre sur la rgle
  this.creeg()
  this.g.setAttribute('visibility', 'hidden')
  this.setPosition(this.x, this.y, this.angle)
  doc.svg.appendChild(this.g)
}
Requerre.prototype = new InstrumentAncetre()
/**
 * Fonction mettant dans this.g l'lment graphique reprsentant la rgle-querre
 * dans le DOM du svg de la figure
 */
Requerre.prototype.creeg = function () {
  let g, p, path, pathtrou, line
  const longd = 172 // Longueur de la partie droite   partir de l'origine
  const longg = 256 // Longueur de la partie gauche   partir de l'origine
  const larg = 57 // La largeur
  const ray = 5 // Rayon de courbure des coins
  const largeurbande = 16 // Largeur des bandes plus claires
  const dectroud = 20
  const dectroub = 15
  const raytrou = 6
  const style = 'stroke:#999999;stroke-width:2; fill: #c6cbe8; fill-opacity: 0.5'

  // Ici on ne peut pas faire un rectangle aux bords arrondis si on veut le petit cercle
  // ajour en bas et   droite

  g = document.createElementNS(kernel.svgsn, 'g')
  p = document.createElementNS(kernel.svgsn, 'path')
  path = 'M 0 0 L ' + String(longd - ray) + ' 0 A ' +
          ray + ' ' + ray + ' 0 0 1 ' + String(longd) + ' ' + ray +
          ' L ' + String(longd) + ' ' + String(larg - ray) + ' A ' +
          ray + ' ' + ray + ' 90 0 1 ' + String(longd - ray) + ' ' + String(larg) +
          ' L ' + String(-longg + ray) + ' ' + larg + ' A ' + ray + ' ' + ray + ' 180 0 1 ' + '-' + longg + ' ' +
          String(larg - ray) + ' L ' + String(-longg) + ' ' + ray + ' A ' + ray + ' ' + ray +
          ' -90 0 1 ' + String(-longg + ray) + ' 0Z'
  // On rajoute le trou parcouru dans le sens inverse pour qu'il ne soit pas rempli
  // Forcment deux demi-cercles. Un cercle entier ne semble pas possible.
  const ytrou = larg - dectroub
  pathtrou = 'M' + String(longd - dectroud + raytrou) + ' ' + ytrou + 'A ' +
          raytrou + ' ' + raytrou + ' 180 0 0 ' + String(longd - dectroud - raytrou) + ' ' + String(ytrou) +
          'A ' + raytrou + ' ' + raytrou + ' 180 0 0 ' + String(longd - dectroud + raytrou) + ' ' + String(ytrou)

  path += pathtrou
  p.setAttribute('d', path)
  p.setAttribute('style', style)
  g.appendChild(p)
  // On rajoute l'intrieur pour les bords soient plus clairs
  p = document.createElementNS(kernel.svgsn, 'path')
  path = 'M' + String(longd) + ' ' + String(largeurbande) + 'L' +
          String(longd) + ' ' + String(larg - ray) + ' A ' +
          ray + ' ' + ray + ' 90 0 1 ' + String(longd - ray) + ' ' + String(larg) +
          ' L ' + String(-longg + ray) + ' ' + larg + ' A ' + ray + ' ' + ray + ' 180 0 1 ' + '-' + longg + ' ' +
          String(larg - ray) + ' L ' + String(-longg) + ' ' + String(largeurbande) + 'Z' + pathtrou
  p.setAttribute('d', path)
  p.setAttribute('style', style)
  g.appendChild(p)
  // Un trait de rappel pour l'origine
  line = document.createElementNS(kernel.svgsn, 'line')
  line.setAttribute('x1', 0)
  line.setAttribute('y1', 8)
  line.setAttribute('x2', 0)
  line.setAttribute('y2', 0)
  line.setAttribute('style', 'stroke: black')
  g.appendChild(line)
  this.gequerre = new Equerre.prototype.creeg()
  this.gequerre.setAttribute('transform', 'scale(0.7)')
  g.appendChild(this.gequerre)
  this.setAbs(this.abscisse)
  this.g = g
}
/**
 * Fonction faisant glisser l'querre sur la rgle jusqu'  l'abscisse abs
 * L'abscisse 0 est reprsente par le petit trait sur la rgle
 * @param {type} abs
 */
Requerre.prototype.setAbs = function (abs) {
  this.abscisse = abs
  this.gequerre.setAttribute('transform', 'translate(' + abs + ',0)')
}
/**
 * Fonction lanant une animation de glissement de l'querre sur la rgle
 * La rgle querre est le seul instrument   voir un mouvement autre que translation et rotation : Le glissement.
 * @param {Float} absfin : l'abscisse de l'querre   la fin du glissement
 * @param {Float} pix10 : le nombre de pixels dont gilsse l'querre
 *   chaque dixime de seconde.
 */
Requerre.prototype.lanceAnimationGlissement = function (absfin, pix10) {
  this.absfin = absfin
  this.pix = (absfin >= this.abscisse) ? pix10 / 4 : -pix10 / 4 // Dans la version JavaScript on quadruple la frquence
  this.dist = Math.abs(absfin - this.abscisse)
  const t = this
  this.timer = setInterval(function () { t.actionPourGlissement() }, 25)
}
/**
 * Fonction appele par un timer lors de l'aniamtion de glissement de l'querre
 * sur la rgle
 */
Requerre.prototype.actionPourGlissement = function () {
  const abs = this.abscisse + this.pix
  const dis = Math.abs(this.absfin - abs)
  if ((dis > this.dist) || !this.doc.animationEnCours) {
    this.setAbs(this.absfin)
    clearInterval(this.timer)
    this.doc.actionSuivante()
  } else {
    this.dist = dis
    this.setAbs(abs)
  }
}
